// services/WebSocketService.js
/**
 * Dedicated WebSocket service for managing socket.io connections
 */
class WebSocketService {
  constructor() {
    this.socket = null;
    this.isConnected = false;
    this.eventHandlers = new Map();
    this.wsUrl = 'http://localhost:5001';
    this.initPromise = null;
    this.reconnectAttempts = 0;
    this.maxReconnectAttempts = 5;
    
    // Listen for token refresh events
    window.addEventListener('tokenRefreshed', (event) => {
      const newToken = event.detail.token;
      if (newToken) {
        this.authenticate(newToken);
      }
    });
    
    // Listen for token expiration
    window.addEventListener('tokenExpired', () => {
      this.disconnect();
    });
  }

  /**
   * Initialize WebSocket connection (singleton pattern)
   */
  async initialize() {
    // If already connected, return success
    if (this.isConnected && this.socket) {
      console.log('WebSocket already connected');
      return true;
    }

    // Return existing promise if already initializing
    if (this.initPromise) {
      console.log('WebSocket initialization already in progress');
      return this.initPromise;
    }

    // Prevent multiple initialization attempts
    console.log('Starting WebSocket initialization...');
    this.initPromise = this._doInitialize();
    return this.initPromise;
  }

  async _doInitialize() {
    try {
      // Disconnect any existing connection first
      if (this.socket) {
        console.log('Cleaning up existing WebSocket connection');
        this.socket.disconnect();
        this.socket = null;
        this.isConnected = false;
      }

      // Try to load socket.io-client
      let io;
      try {
        // First try the standard import
        const module = await import('socket.io-client');
        io = module.default || module.io;
      } catch (e) {
        console.error('Failed to load socket.io-client:', e);
        // Try alternative import method
        if (window.io) {
          io = window.io;
        } else {
          throw new Error('socket.io-client not available');
        }
      }

      // Get auth token from localStorage
      const token = localStorage.getItem('token');
      
      // Check if token exists and is not expired
      let validToken = null;
      if (token) {
        try {
          // Decode JWT to check expiration
          const payload = JSON.parse(atob(token.split('.')[1]));
          const expiresAt = payload.exp * 1000; // Convert to milliseconds
          const now = Date.now();
          
          if (expiresAt > now) {
            validToken = token;
          } else {
            console.warn('Token expired, connecting without authentication');
          }
        } catch (e) {
          console.error('Error decoding token:', e);
        }
      }

      // Create socket connection with auth and reduced reconnection
      this.socket = io(this.wsUrl, {
        transports: ['polling', 'websocket'],
        reconnection: true,
        reconnectionDelay: 2000,
        reconnectionDelayMax: 10000,
        reconnectionAttempts: 3, // Reduced from 5 to prevent spam
        timeout: 10000, // Reduced timeout
        autoConnect: true,
        forceNew: true, // Force new connection
        auth: {
          token: validToken || ''
        },
        extraHeaders: {
          'Authorization': validToken ? `Bearer ${validToken}` : ''
        }
      });
      
      console.log('WebSocket connection created with config:', {
        hasToken: !!validToken,
        reconnectionAttempts: 3,
        timeout: 10000
      });

      // Set up event handlers
      this.setupEventHandlers();

      // Wait for connection (but don't fail if it times out)
      const connected = await this.waitForConnection();
      
      if (!connected) {
        console.log('WebSocket connection timed out - app will continue without real-time updates');
      }

      return connected;
    } catch (error) {
      console.error('WebSocket initialization failed:', error);
      this.socket = null;
      return false;
    }
  }

  /**
   * Wait for socket connection with timeout
   */
  waitForConnection(timeout = 5000) {
    return new Promise((resolve) => {
      if (this.isConnected) {
        resolve(true);
        return;
      }

      const timer = setTimeout(() => {
        console.warn('WebSocket connection timeout - continuing without WebSocket support');
        this.socket.off('connect', onConnect);
        resolve(false); // Resolve with false instead of rejecting
      }, timeout);

      const onConnect = () => {
        clearTimeout(timer);
        this.socket.off('connect', onConnect);
        resolve(true);
      };

      this.socket.on('connect', onConnect);
    });
  }

  /**
   * Setup core event handlers
   */
  setupEventHandlers() {
    if (!this.socket) return;

    // Remove any existing listeners to prevent duplicates
    this.socket.removeAllListeners();

    this.socket.on('connect', () => {
      console.log('WebSocket connected successfully');
      this.isConnected = true;
      this.reconnectAttempts = 0;
      this.initPromise = null; // Reset init promise
      this.emit('connected');
    });

    this.socket.on('disconnect', (reason) => {
      console.log('WebSocket disconnected:', reason);
      this.isConnected = false;
      this.emit('disconnected', reason);
      
      // Only attempt reconnection for certain reasons
      if (reason === 'io server disconnect' || reason === 'transport close') {
        console.log('Server initiated disconnect - not attempting reconnection');
        this.initPromise = null;
      }
    });

    this.socket.on('connect_error', (error) => {
      console.error('WebSocket connection error:', error.message);
      this.reconnectAttempts++;
      
      if (this.reconnectAttempts >= 3) { // Use hardcoded value
        console.error('Max reconnection attempts reached - giving up');
        this.socket.disconnect();
        this.initPromise = null;
        this.emit('max_reconnect_failed');
      }
    });

    this.socket.on('error', (error) => {
      console.error('WebSocket error:', error);
      this.emit('error', error);
    });

    // Add reconnect handler
    this.socket.on('reconnect', (attemptNumber) => {
      console.log(`WebSocket reconnected after ${attemptNumber} attempts`);
      this.isConnected = true;
      this.emit('reconnected');
    });

    this.socket.on('reconnect_failed', () => {
      console.error('WebSocket reconnection failed completely');
      this.initPromise = null;
      this.emit('reconnect_failed');
    });
  }

  /**
   * Subscribe to socket events
   */
  on(event, handler) {
    if (!this.eventHandlers.has(event)) {
      this.eventHandlers.set(event, new Set());
    }
    this.eventHandlers.get(event).add(handler);

    // Also subscribe to socket event if connected
    if (this.socket && this.isConnected) {
      this.socket.on(event, handler);
    }
  }

  /**
   * Unsubscribe from socket events
   */
  off(event, handler) {
    if (this.eventHandlers.has(event)) {
      this.eventHandlers.get(event).delete(handler);
    }

    // Also unsubscribe from socket event
    if (this.socket) {
      this.socket.off(event, handler);
    }
  }

  /**
   * Emit local event
   */
  emit(event, data) {
    if (this.eventHandlers.has(event)) {
      this.eventHandlers.get(event).forEach(handler => {
        try {
          handler(data);
        } catch (error) {
          console.error(`Error in event handler for ${event}:`, error);
        }
      });
    }
  }

  /**
   * Send message to server
   */
  send(event, data) {
    if (this.socket && this.isConnected) {
      this.socket.emit(event, data);
      return true;
    } else {
      console.warn('Cannot send message - WebSocket not connected');
      return false;
    }
  }

  /**
   * Join a room
   */
  joinRoom(room) {
    return this.send('join_room', { room });
  }

  /**
   * Leave a room
   */
  leaveRoom(room) {
    return this.send('leave_room', { room });
  }

  /**
   * Authenticate socket connection
   */
  authenticate(token) {
    if (this.socket) {
      // Update auth for future connections
      this.socket.auth = { token };
      
      // If already connected, disconnect and reconnect with new auth
      if (this.isConnected) {
        this.socket.disconnect();
        
        // Update connection options
        this.socket.io.opts.auth = { token };
        this.socket.io.opts.extraHeaders = {
          'Authorization': token ? `Bearer ${token}` : ''
        };
        
        // Reconnect with new auth
        this.socket.connect();
      }
    }
  }

  /**
   * Disconnect socket
   */
  disconnect() {
    console.log('Disconnecting WebSocket...');
    
    if (this.socket) {
      // Remove all listeners before disconnecting
      this.socket.removeAllListeners();
      this.socket.disconnect();
      this.socket = null;
    }
    
    this.isConnected = false;
    this.initPromise = null;
    this.reconnectAttempts = 0;
    
    console.log('WebSocket disconnected and cleaned up');
  }

  /**
   * Check if connected
   */
  get connected() {
    return this.isConnected;
  }
}

// Export singleton instance
const webSocketService = new WebSocketService();
export default webSocketService;